/*eslint-disable no-undef*/
let background
try {
  background = chrome.extension.getBackgroundPage();
} catch(e) {
  // mock
  background = require('./mock').background
}

export default {
  proxyTranslatorActivate(cardNum, secret) {
    return background.proxyTranslator.activate(cardNum, secret)
  },

  findProxyTranslatorlicenseInfo(cardNum) {
    return background.proxyTranslator.licenseInfo(cardNum)
  },

  findProxyCurrentLicense() {
    return background.proxyTranslator.currentLicense()
  }
}
